import {
    Component,
    h,
    Element,
    Prop,
    State,
    Listen,
    Event,
    EventEmitter,
} from '@stencil/core';
import {
    LimeWebComponent,
    LimeWebComponentContext,
    LimeWebComponentPlatform,
} from '@limetech/lime-web-components';
import { EnumViews } from '../../models/EnumViews';
import { IDocument } from '../../types/Document';
import { IRecipient } from '../../types/Recipient';
import { ISession } from '../../types/Session';
import {
    fetchMe,
    fetchEntity,
    refreshToken,
    fetchSentDocuments,
} from '../../services';
import { IEntity } from '../../types/Entity';
import { ICustomField } from '../../types/CustomField';
import { IListItem } from '../../types/ListItem';
import { workflowSteps } from '../workflow-progress-bar/workflow-steps';
import { ITemplateRole } from 'src/types/TemplateRole';

@Component({
    tag: 'lwc-getaccept-root',
    shadow: true,
    styleUrl: 'lwc-getaccept-root.scss',
})
export class Root implements LimeWebComponent {
    @Element() public element: HTMLLwcGetacceptRootElement;
    @Prop() public platform: LimeWebComponentPlatform;
    @Prop() public context: LimeWebComponentContext;
    @Prop() public company: any;

    @State() private externalId: string;
    @State() private isOpen: boolean = false;
    @State() private session: ISession;
    @State() private user;
    @State() public entities: IEntity[] = [];
    @State() public documentId: string;
    @State() public activeView = EnumViews.login;

    @State() public documentData: IDocument;
    @State() public isSealed: boolean;

    @State() public template: IListItem;
    @State() public limeDocument: IListItem;
    @State() public templateFields: ICustomField[];
    @State() public templateRoles: ITemplateRole[];
    @State() public errorMessage: string = '';

    @State() public documents: IDocument[] = [];
    @State() public isLoadingDocuments: boolean;
    @State() public isSending: boolean = false;

    @Event() public errorHandler: EventEmitter<string>;

    constructor() {
        this.openDialog = this.openDialog.bind(this);
        this.handleLogoClick = this.handleLogoClick.bind(this);
        this.renderLayout = this.renderLayout.bind(this);
        this.loadInitialData = this.loadInitialData.bind(this);
        this.loadSentDocuments = this.loadSentDocuments.bind(this);
        this.showWorkflow = this.showWorkflow.bind(this);
    }

    public componentWillLoad() {
        this.externalId = `${this.context.limetype}_${this.context.id}`;
        this.activeView = this.checkIfSessionExists
            ? EnumViews.home
            : EnumViews.login;

        if (this.session) {
            this.loadInitialData();
        }

        this.setDefaultDocumentData();
    }

    private async loadInitialData() {
        this.loadSentDocuments();

        try {
            const { user, entities } = await fetchMe(
                this.platform,
                this.session
            );
            this.user = user;
            this.entities = entities;
        } catch (e) {
            this.errorHandler.emit(
                'Could not load user session. Try relogging'
            );
        }

        this.loadEntityDetails();
    }

    private async loadEntityDetails() {
        try {
            const { entity } = await fetchEntity(this.platform, this.session);

            this.session.entity_id = entity.id;

            this.documentData.email_send_message =
                entity.email_send_message !== ''
                    ? entity.email_send_message
                    : entity.default_email_send_message;
            this.documentData.email_send_subject =
                entity.email_send_subject !== ''
                    ? entity.email_send_subject
                    : entity.default_email_send_subject;
        } catch (e) {
            this.errorHandler.emit(
                'Could not load user session. Try relogging'
            );
        }
    }

    public render() {
        return [
            <limel-flex-container class="actionpad-container">
                <button class="getaccept-button" onClick={this.openDialog}>
                    <img src="https://static-vue-rc.getaccept.com/img/integrations/logo_only.png" />
                    <span class="button-text">Send document</span>

                    {this.renderDocumentCount(!!this.session)}
                </button>
            </limel-flex-container>,
            <limel-dialog
                open={this.isOpen}
                closingActions={{ escapeKey: true, scrimClick: false }}
                onClose={() => {
                    this.isOpen = false;
                }}
                class={{ aside: this.checkIsAside() }}
            >
                <div class="ga-top-bar">
                    {this.renderLogo(this.showWorkflow())}
                    <limel-icon
                        class="close"
                        name="cancel"
                        size="small"
                        onClick={() => {
                            this.isOpen = false;
                        }}
                    />
                    {(() => {
                        if (this.activeView !== EnumViews.login) {
                            return [
                                <layout-menu
                                    activeView={this.activeView}
                                    isSending={this.isSending}
                                />,
                                <workflow-progress-bar
                                    isVisible={this.showWorkflow()}
                                    activeView={this.activeView}
                                />,
                            ];
                        }
                    })()}
                </div>

                <div class="ga-body">{this.renderLayout()}</div>
                <div class="ga-version">
                    <span>Version: 1.3.13</span>
                </div>
                <limel-button-group slot="button">
                    <limel-button
                        label="Cancel"
                        onClick={() => {
                            this.isOpen = false;
                        }}
                    />
                </limel-button-group>
                <error-message error={this.errorMessage} />
            </limel-dialog>,
        ];
    }

    private checkIsAside() {
        return (
            (this.element as any).parentNode?.parentNode?.parentNode?.parentNode
                ?.host?.parentNode?.parentNode?.host?.parentNode?.parentNode
                ?.host?.parentNode?.tagName === 'ASIDE'
        );
    }

    private renderDocumentCount(hasSession: boolean) {
        if (hasSession) {
            return (
                <span class="document-count">
                    <limel-icon name="file" size="small" />
                    <span>{this.documents.length}</span>
                </span>
            );
        }

        return [];
    }

    private renderLogo(compact) {
        const classes: string = `logo-container ${compact ? 'compact' : ''}`;

        return (
            <div class={classes}>
                <img
                    onClick={this.handleLogoClick}
                    src="https://static-vue-rc.getaccept.com/img/integrations/logo-inverted.png"
                    class="logo"
                />
            </div>
        );
    }

    private showWorkflow(): boolean {
        if (this.isSending || this.isSealed) {
            return false;
        }

        return workflowSteps.some(view => view.currentView === this.activeView);
    }

    private renderLayout() {
        switch (this.activeView) {
            case EnumViews.home:
                return (
                    <layout-overview
                        platform={this.platform}
                        session={this.session}
                        externalId={this.externalId}
                        documents={this.documents}
                        isAside={this.checkIsAside()}
                    />
                );
            case EnumViews.login:
                return <layout-login platform={this.platform} />;
            case EnumViews.selectFile:
                return (
                    <layout-select-file
                        platform={this.platform}
                        session={this.session}
                        context={this.context}
                        selectedLimeDocument={this.limeDocument}
                        selectedTemplate={this.template}
                        customFields={this.templateFields}
                        templateRoles={this.templateRoles}
                    />
                );
            case EnumViews.templateRoles:
                return (
                    <layout-template-roles
                        platform={this.platform}
                        session={this.session}
                        context={this.context}
                        document={this.documentData}
                        limeDocument={this.limeDocument}
                        template={this.template}
                        templateRoles={this.templateRoles}
                    />
                );
            case EnumViews.recipient:
                return (
                    <layout-select-recipient
                        platform={this.platform}
                        document={this.documentData}
                        context={this.context}
                        isAside={this.checkIsAside()}
                    />
                );
            case EnumViews.settings:
                return (
                    <layout-settings
                        user={this.user}
                        entities={this.entities}
                        session={this.session}
                        platform={this.platform}
                    />
                );
            case EnumViews.help:
                return <layout-help />;
            case EnumViews.sendDocument:
                return (
                    <layout-send-document
                        context={this.context}
                        document={this.documentData}
                        limeDocument={this.limeDocument}
                        template={this.template}
                        session={this.session}
                        platform={this.platform}
                    />
                );
            case EnumViews.videoLibrary:
                return (
                    <layout-video-library
                        platform={this.platform}
                        session={this.session}
                    />
                );
            case EnumViews.documentDetail:
                return (
                    <layout-document-details
                        platform={this.platform}
                        session={this.session}
                        documentId={this.documentId}
                    />
                );
            case EnumViews.documentValidation:
                return (
                    <layout-validate-document
                        platform={this.platform}
                        session={this.session}
                        document={this.documentData}
                        limeDocument={this.limeDocument}
                        template={this.template}
                        fields={this.templateFields}
                        isSealed={this.isSealed}
                        isSending={this.isSending}
                        templateRoles={this.templateRoles}
                    />
                );
            default:
                return <layout-overview />;
        }
    }

    private logout() {
        localStorage.removeItem('getaccept_session');
        this.documents = [];
        this.activeView = EnumViews.login;
    }

    private openDialog(): void {
        this.isOpen = true;
        if (this.checkIsPreviewView) {
            this.element.shadowRoot.lastElementChild.classList.add(
                'preview-view'
            );
        }
    }

    private handleLogoClick(): void {
        this.activeView = this.checkIfSessionExists
            ? EnumViews.home
            : EnumViews.login;
    }

    private async loadSentDocuments() {
        this.isLoadingDocuments = true;
        try {
            this.documents = await fetchSentDocuments(
                this.platform,
                this.externalId,
                this.session
            );
        } catch (e) {
            this.errorHandler.emit(
                'Something went wrong while documents from GetAccept...'
            );
        }

        this.isLoadingDocuments = false;
    }

    @Listen('changeView')
    changeViewHandler(view: CustomEvent) {
        if (view.detail === EnumViews.logout) {
            this.logout();
        } else if (this.isSealed) {
            this.activeView = EnumViews.home;
            this.setDefaultDocumentData();
        } else {
            this.activeView = view.detail;
        }
    }

    @Listen('loadRelatedDocuments')
    loadRealtedDocumentsHandler() {
        this.loadSentDocuments();
    }

    @Listen('setTemplate')
    setTemplate(event) {
        this.template = event.detail;
        this.documentData.name = event.detail.text;
        this.limeDocument = null;
        this.templateFields = [];
    }

    @Listen('setLimeDocument')
    setLimeDocument(event) {
        this.limeDocument = event.detail;
        this.documentData.name = event.detail.text;
        this.template = null;
        this.templateRoles = null;
        this.templateFields = [];
    }

    @Listen('setCustomFields')
    setCustomFields(event) {
        this.templateFields = event.detail;
    }

    @Listen('setTemplateRoles')
    setTemplateRoles(event) {
        this.templateRoles = event.detail;
        this.documentData = {
            ...this.documentData,
            recipients: this.documentData.recipients.map(recipient => ({
                ...recipient,
                role_id: '',
            })),
        };
    }

    @Listen('updateDocumentRecipient')
    updateDocumentRecipientHandler(recipients: CustomEvent<IRecipient[]>) {
        this.documentData.recipients = recipients.detail;
    }

    @Listen('setDocumentType')
    documentTypeHandler(isSigning: CustomEvent<boolean>) {
        this.documentData.is_signing = isSigning.detail;
    }

    @Listen('setSession')
    setSessionHandler(sessionData: CustomEvent<ISession>) {
        this.setSessionData(sessionData.detail);
        this.activeView = EnumViews.home;
        this.loadInitialData();
    }

    @Listen('setNewDocumentName')
    setDocumentName(documentName: CustomEvent<string>) {
        this.documentData = {
            ...this.documentData,
            name: documentName.detail,
        };
    }

    @Listen('setDocumentValue')
    setDocumentValue(value: CustomEvent<number>) {
        this.documentData = {
            ...this.documentData,
            value: value.detail,
        };
    }

    @Listen('setSmartReminder')
    setDocumentSmartReminder(smartReminder: CustomEvent<boolean>) {
        this.documentData.is_reminder_sending = smartReminder.detail;
    }

    @Listen('setIsSmsSending')
    setDocumentIsSmsSending(isSmsSending: CustomEvent<boolean>) {
        this.documentData.is_sms_sending = isSmsSending.detail;
    }

    @Listen('setEmailSubject')
    setDocumentEmailSubject(emailSendSubject: CustomEvent<string>) {
        this.documentData.email_send_subject = emailSendSubject.detail;
    }

    @Listen('setEmailMessage')
    setDocumentEmailMessage(emailSendMessage: CustomEvent<string>) {
        this.documentData.email_send_message = emailSendMessage.detail;
    }

    @Listen('validateDocument')
    validateDocumentHandler() {
        this.activeView = EnumViews.documentValidation;
    }

    @Listen('openDocument')
    openDocumentDetails(document: CustomEvent<IDocument>) {
        this.activeView = EnumViews.documentDetail;
        this.documentId = document.detail.id;
    }

    @Listen('recipientRoleUpdated')
    updateRecipientRole(
        event: CustomEvent<{ recipient: IRecipient; role: ITemplateRole }>
    ) {
        const newRecipients = [...this.documentData.recipients];
        const recipientIndex = newRecipients.findIndex(
            recipient =>
                recipient.email === event.detail.recipient.email &&
                recipient.lime_id === event.detail.recipient.lime_id
        );
        newRecipients[recipientIndex].role_id = event.detail.role.role_id;
        this.documentData = {
            ...this.documentData,
            recipients: [...newRecipients],
        };
    }

    @Listen('setVideo')
    setDocumentVideo(videoId: CustomEvent<string>) {
        this.documentData.video_id = videoId.detail;
        this.documentData.is_video = true;
    }

    @Listen('removeVideo')
    removeDocumentVideo() {
        this.documentData.video_id = '';
        this.documentData.is_video = false;
    }

    @Listen('isSendingDocument')
    setIsSending(isSending: CustomEvent<boolean>) {
        this.isSending = isSending.detail;
    }

    @Listen('documentCompleted')
    documentCompleted(isSealed: CustomEvent<boolean>) {
        this.setDefaultDocumentData();
        this.loadEntityDetails();
        this.isSealed = isSealed.detail;
        if (!this.isSealed) {
            this.activeView = EnumViews.home;
        }

        this.loadSentDocuments();
    }

    @Listen('errorHandler')
    onError(event: CustomEvent<string>) {
        this.errorMessage = event.detail;
        // eslint-disable-next-line @typescript-eslint/no-magic-numbers
        setTimeout(() => (this.errorMessage = ''), 100); // Needed for same consecutive error message
    }

    get checkIfSessionExists(): boolean {
        const storedSession = window.localStorage.getItem('getaccept_session');
        if (storedSession) {
            const sessionObj: ISession = JSON.parse(storedSession);

            // used to check if token should be refreshed or not.
            this.validateToken(sessionObj);
            this.session = sessionObj;
        }

        return !!storedSession;
    }

    get checkIsPreviewView(): boolean {
        return document
            .querySelector('lime-webclient')
            .shadowRoot.activeElement.classList.contains('has-side-panel-open');
    }

    async validateToken(session: ISession) {
        try {
            const { data, success } = await refreshToken(
                this.platform,
                session
            );
            if (success) {
                const storedSession =
                    window.localStorage.getItem('getaccept_session');
                if (storedSession) {
                    const sessionObj: ISession = JSON.parse(storedSession);
                    sessionObj.expires_in = data.expires_in;
                    sessionObj.access_token = data.access_token;
                    this.setSessionData(sessionObj);
                }
            } else {
                this.errorMessage = 'Could not refresh token.';
                setTimeout(() => (this.errorMessage = ''));
            }
        } catch (error) {
            this.errorMessage = 'Could not refresh token.';
            setTimeout(() => (this.errorMessage = ''));
            this.logout();
        }

        return true;
    }

    private setSessionData(session: ISession) {
        window.localStorage.setItem(
            'getaccept_session',
            JSON.stringify(session)
        );
        this.session = session;
    }

    setDefaultDocumentData() {
        this.documentData = {
            is_signing: false,
            name: '',
            recipients: [],
            template_id: '',
            custom_fields: [],
            is_reminder_sending: false,
            is_sms_sending: false,
            email_send_subject: '',
            email_send_message: '',
            video_id: '',
            is_video: false,
            external_id: this.externalId,
            value: 0,
        };
        this.templateFields = [];
        this.isSealed = false;
        this.template = null;
        this.limeDocument = null;
    }
}
